.library{
  position: fixed;
  top: 0;
  left: 0;
  width: 20rem;
  height: 100%;
  background: white;
  box-shadow: 2px 2px 50px rgb(204,204,204);
  overflow: scroll;
  transition: all 0.7s ease;
  transform: translateX(-100%);
  opacity: 0;
  h2{
    padding: 2rem;
  }
}
.active-library{
  transform: translateX(0);
  opacity: 1;
}

.library-song{
  display:flex;
  align-items:center;
  padding: 1rem 2rem;
  cursor: pointer;
  transition: background 0.7s ease;
  img{
    width: 30%;
  }
  &:hover{
    background: rgb(219, 213, 255);
  }
}

.song-description{
  padding-left: 1rem; 
  h3{
    font-size: 1.2rem;
  }
  h4{
    font-size: 0.7rem;
  }
}

*{
  scrollbar-width: thin;
  scrollbar-color: rgba(155,155,155,0.5) transparent;
}

*::-webkit-scrollbar{
  width:5px;
}
*::-webkit-scrollbar-track{
  background: transparent;
}
*::-webkit-scrollbar-thumb{
  background-color: rgba(155,155,155,0.5);
  border-radius: 20px;
  border:transparent;
}

.selected{
  background-color: rgb(214, 199, 255)
}

@media screen and (max-width:768px){
  .library{
    width: 100%;
  }
}